body{
    margin: 0;

    font-family: Montserrat, sans-serif;
    letter-spacing: 0.02em;
    color: $primary-text;

    background-color: $primary-bg;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

h1, h2, h3, h4, h5, h6, p
{
    margin: 0;
    padding: 0;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
li{
    margin: 0;
    padding: 0;
}
a{
    text-decoration: none;
}
